body{
    color: wheat;
    background-color: black;
    width: 100%;
    min-height: 100vh;
    height: 100%;
}
.heading-design{
    width: 100%;
    display: flex;
    justify-content: space-between;
}
.avatar, .name-heading{
    margin-top: 6vh;
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
}
.avatar-comp{
    height: 90px;
    width: 90px;
}
.icon-btn, .card-container, .subtext-container{
    padding-top: 4vh;
    display: flex;
    flex-direction: row;
    gap: 16px;
}
.card-design{
    height: 90px;
    width: 130px;
    background-color: #333 !important;
    color: aliceblue !important;
    padding: 10px;
    padding-left: 18px;
}
.subtext-container{
    padding-top: 4vh;
    display: flex;
    flex-direction: row;
    gap: 1vw;
}
.hire-me-btn{
    margin-top: 4vh;
    border: 1px solid #fff !important;
    padding: 14px;
    border-radius: 10px;
    cursor: pointer;
}
.hire-me-btn:hover{
    border-radius: 10px;
    color: aliceblue !important;
}
.icon-btn-se{
    background-color: wheat !important;
    color: black !important;
}
.icon-btn-se:hover{
    background-color: #fff !important;
}
.image-logo, .menu-icon, .card-icon{
    color: wheat;
}
.menu-icon:hover{
    color: white;
}
.project-count{
    font-size: 3vh;
}
.card-subtext{
    font-size: 2.6vh;
}